@import "palettes.scss";

.btn {
    font-size: 1rem;
    padding: 0.2em 1.2em;
    background-color: transparent;
    color: map-get($map: $primary-palette, $key: A500);
    border: none;
    border-radius: 1em;
    text-shadow: 0.1em 0.1em 0.8em black;
    cursor: pointer;
    transition: ease all 0.4s;
}

.btn:hover {
    color: map-get($map: $primary-palette, $key: A50);
}

.btn:active {
    color: map-get($map: $primary-palette, $key: A700);
}

.btn-primary {
    background-color: map-get($map: $primary-palette, $key: A500);
    color: white;
    border: none;
    transition: ease all 0.2s;
}

.btn-primary:hover {
    background-color: map-get($map: $primary-palette, $key: A300);
    color: white;
    text-shadow: 2px 2px 10px black;
}

.btn-primary:active {
    background-color: map-get($map: $primary-palette, $key: A900);
    border-color: map-get($map: $primary-palette, $key: A900);
}

.btn-secondary {
    background-color: transparent;
    color: map-get($map: $primary-palette, $key: A500);
    border-width: 1px;
    border-color: map-get($map: $primary-palette, $key: A500);
    border-radius: 1em;
    border-style: solid;
    transition: ease all 0.2s;
}

.btn-secondary:hover {
    background-color: map-get($map: $primary-palette, $key: A600);
    text-shadow: 0em 0em 0.3em black;
    color: white;
}

.btn-secondary:active {
    background-color: map-get($map: $primary-palette, $key: A700);
    border-color: map-get($map: $primary-palette, $key: A900);
}

.btn-sm {
    padding-left: 0.8em;
    padding-right: 0.8em;
}
