.menu {
    padding: 2rem 0;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    .menu-header {
        grid-column: span 12;
        display: flex;
        align-items: baseline;

        h2 {
            margin: 0;
            padding-right: 1rem;
        }
    }

    @media (max-width: 800px) {
        .menu-header {
            display: block;
            text-align: center;
            h2 {
                padding: 0;
            }
        }
    }
}
