@import "framework/palettes.scss";

$darkness: 0.7;

.header {
    position: fixed;
    width: 100%;
    height: auto;
    margin: 0;
    padding: 0.5rem 1rem 0rem 1rem;
    box-shadow: 0px 0.2vh 1rem 1rem rgba(0, 0, 0, $darkness);
    background-color: rgba(0, 0, 0, $darkness);
    text-transform: lowercase;
    .toggle {
        display: none;
        border: 0;
        padding: 0rem;
        background-color: transparent;
        color: white;

        transition: all 0.2s ease;
        svg {
            display: block;
            font-size: 1.1rem;
        }
    }

    .toggle:active {
        transform: rotate(180deg);
        transition: transform 0.2s ease;
        color: map-get($map: $primary-palette, $key: A500);
    }
    nav {
        a {
            color: white;
            display: inline-block;
            padding: 0.5rem;
            text-decoration: none;
            transition: ease 0.5s;
        }

        a:hover {
            color: map-get($map: $primary-palette, $key: A500);
            text-shadow: 0em 0em 0.2em black;
        }
    }

    nav.responsive {
        max-height: 100vh;
        transition: max-height 0.2s ease-in;
        a {
            display: block;
        }
    }

    @media (max-width: 600px) {
        .toggle {
            display: inline-block;
        }
        nav {
            max-height: 0;
            overflow: hidden;
            transition: max-height 0.2s ease-out;
            a {
                display: block;
            }
        }
    }
}
