.grid {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
}

.grid-fluid {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(10rem, 1fr));
}

.row {
    display: grid;
    grid-column: span 12;
    grid-template-columns: repeat(12, 1fr);
}

.gap {
    gap: 1rem;
}

.gap > .row {
    gap: 1rem;
}

.gap-1-5 {
    gap: 1.5rem;
}

.gap-2 {
    gap: 2rem;
}

/* #region Grid Columns */
.col-2 {
    grid-column: span 2;
}

.col-3 {
    grid-column: span 3;
}

.col-4 {
    grid-column: span 4;
}

.col-5 {
    grid-column: span 5;
}

.col-6 {
    grid-column: span 6;
}

.col-7 {
    grid-column: span 7;
}

.col-8 {
    grid-column: span 8;
}

.col-9 {
    grid-column: span 9;
}

.col-10 {
    grid-column: span 10;
}

.col-11 {
    grid-column: span 11;
}

.col-12 {
    grid-column: span 12;
}
/* #endregion */

@media (max-width: 800px) {
    .grid > *,
    .row > * {
        grid-column: span 12;
    }
}
