@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@400;500;600;700&display=swap);
*,*::before,*::after{box-sizing:border-box}html,body{margin:0;border:0;padding:0}html{font-size:1.4rem;font-family:"Dosis", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}body{background-image:linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.6)),url(/static/media/background.54634bc3.jpg);background-repeat:no-repeat;background-attachment:fixed;color:white;overflow-y:auto}main{padding-top:5rem}h5{font-size:1.1rem;margin:0 0 0.5rem 0}p{margin:0 0 1rem 0}

.logo{display:block;margin:auto;padding:2.5rem 0}.tagline{padding:1rem 0 4rem 0;text-transform:lowercase}.tagline h1{font-size:clamp(2rem, 5vw, 5rem)}.card{text-align:center;text-transform:lowercase;font-size:clamp(0.9rem, 1vw, 1.4rem)}.card h1{font-size:clamp(1.2rem, 3vw, 4rem)}.hours-section div{padding:0}.hours-section div p{display:inline-block;margin:0;padding-bottom:1rem}.hours-section div p:first-child{width:50%;padding:0 1rem 0 0;text-align:right}.hours-section div p:last-child{width:50%;padding:0 0 0 0.5rem;text-align:left}@media (max-width: 400px){.hours-section div p:first-child,.hours-section div p:last-child{display:block;width:auto;padding:0;text-align:center}.hours-section div p:first-child{font-weight:600}.hours-section div p:last-child{padding-bottom:0.5rem}}.footer{text-align:center;padding:1.5rem 0;margin-top:3rem;width:100%}main{text-shadow:0.4em 0.4em 0.5em black;padding:5% 5% 0 5%}

.grid{width:100%;display:grid;grid-template-columns:repeat(12, 1fr)}.grid-fluid{width:100%;display:grid;grid-template-columns:repeat(auto-fit, minmax(10rem, 1fr))}.row{display:grid;grid-column:span 12;grid-template-columns:repeat(12, 1fr)}.gap{grid-gap:1rem;gap:1rem}.gap>.row{grid-gap:1rem;gap:1rem}.gap-1-5{grid-gap:1.5rem;gap:1.5rem}.gap-2{grid-gap:2rem;gap:2rem}.col-2{grid-column:span 2}.col-3{grid-column:span 3}.col-4{grid-column:span 4}.col-5{grid-column:span 5}.col-6{grid-column:span 6}.col-7{grid-column:span 7}.col-8{grid-column:span 8}.col-9{grid-column:span 9}.col-10{grid-column:span 10}.col-11{grid-column:span 11}.col-12{grid-column:span 12}@media (max-width: 800px){.grid>*,.row>*{grid-column:span 12}}.btn{font-size:1rem;padding:0.2em 1.2em;background-color:transparent;color:#f99909;border:none;border-radius:1em;text-shadow:0.1em 0.1em 0.8em black;cursor:pointer;transition:ease all 0.4s}.btn:hover{color:#fff8ee}.btn:active{color:#d98506}.btn-primary{background-color:#f99909;color:white;border:none;transition:ease all 0.2s}.btn-primary:hover{background-color:#faa82e;color:white;text-shadow:2px 2px 10px black}.btn-primary:active{background-color:#b56f05;border-color:#b56f05}.btn-secondary{background-color:transparent;color:#f99909;border-width:1px;border-color:#f99909;border-radius:1em;border-style:solid;transition:ease all 0.2s}.btn-secondary:hover{background-color:#eb9006;text-shadow:0em 0em 0.3em black;color:white}.btn-secondary:active{background-color:#d98506;border-color:#b56f05}.btn-sm{padding-left:0.8em;padding-right:0.8em}.padx-1{padding-left:1rem;padding-right:1rem}.pady-1{padding-top:1rem;padding-bottom:1rem}.padx-2{padding-left:2rem;padding-right:2rem}.pady-2{padding-top:2rem;padding-bottom:2rem}.center-text{text-align:center}.left-text{text-align:left}

.menu{padding:2rem 0;display:grid;grid-template-columns:repeat(12, 1fr)}.menu .menu-header{grid-column:span 12;display:flex;align-items:baseline}.menu .menu-header h2{margin:0;padding-right:1rem}@media (max-width: 800px){.menu .menu-header{display:block;text-align:center}.menu .menu-header h2{padding:0}}

.header{position:fixed;width:100%;height:auto;margin:0;padding:0.5rem 1rem 0rem 1rem;box-shadow:0px 0.2vh 1rem 1rem rgba(0,0,0,0.7);background-color:rgba(0,0,0,0.7);text-transform:lowercase}.header .toggle{display:none;border:0;padding:0rem;background-color:transparent;color:white;transition:all 0.2s ease}.header .toggle svg{display:block;font-size:1.1rem}.header .toggle:active{transform:rotate(180deg);transition:transform 0.2s ease;color:#f99909}.header nav a{color:white;display:inline-block;padding:0.5rem;text-decoration:none;transition:ease 0.5s}.header nav a:hover{color:#f99909;text-shadow:0em 0em 0.2em black}.header nav.responsive{max-height:100vh;transition:max-height 0.2s ease-in}.header nav.responsive a{display:block}@media (max-width: 600px){.header .toggle{display:inline-block}.header nav{max-height:0;overflow:hidden;transition:max-height 0.2s ease-out}.header nav a{display:block}}

