.logo {
    display: block;
    margin: auto;
    padding: 2.5rem 0;
}

.tagline {
    padding: 1rem 0 4rem 0;
    text-transform: lowercase;
    h1 {
        font-size: clamp(2rem, 5vw, 5rem);
    }
}

.card {
    text-align: center;
    text-transform: lowercase;
    font-size: clamp(0.9rem, 1vw, 1.4rem);
    h1 {
        font-size: clamp(1.2rem, 3vw, 4rem);
    }
}

.hours-section {
    div {
        padding: 0;
        p {
            display: inline-block;
            margin: 0;
            padding-bottom: 1rem;
        }
        p:first-child {
            width: 50%;
            padding: 0 1rem 0 0;
            text-align: right;
        }
        p:last-child {
            width: 50%;
            padding: 0 0 0 0.5rem;
            text-align: left;
        }
        @media (max-width: 400px) {
            p:first-child,
            p:last-child {
                display: block;
                width: auto;
                padding: 0;
                text-align: center;
            }
            p:first-child {
                font-weight: 600;
            }
            p:last-child {
                padding-bottom: 0.5rem;
            }
        }
    }
}

.footer {
    text-align: center;
    padding: 1.5rem 0;
    margin-top: 3rem;
    width: 100%;
}

main {
    text-shadow: 0.4em 0.4em 0.5em black;
    padding: 5% 5% 0 5%;
}
